body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #00447c;
  color: white;
  padding: 10px 20px;
}

.navbar .logo {
  font-weight: bold;
  font-size: 1.5em;
}

.navbar nav a {
  color: white;
  margin: 0 10px;
  text-decoration: none;
}

.navbar nav a:hover {
  text-decoration: underline;
}

.hero-section {
  text-align: center;
  padding: 50px;
  background: linear-gradient(to bottom, #00447c, #0073e6);
  color: white;
}

.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.search-row {
  display: flex;
  gap: 10px;
}

/* Input styles for text and date fields */
.search-row input {
  padding: 10px;
  width: 220px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  color: #333;
  background-color: white;
}

/* Placeholder text style */
.search-row input::placeholder {
  color: lightgray;
}

/* Specific style for date input to ensure consistent look */
.search-row input[type="date"] {
  appearance: none;
}

/* Focus effect */
.search-row input:focus {
  outline: none;
  border-color: #0073e6;
}

.search-form button {
  padding: 10px 20px;
  background-color: #ff6600;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.promotions {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.promo-card {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 10px;
  padding: 20px;
  text-align: center;
  width: 300px;
}

.footer {
  text-align: center;
  padding: 10px;
  background-color: #00447c;
  color: white;
  font-size: 0.9em;
}
